import store from "@/store"
import { Message } from 'element-ui'

export default {
  getBase64: (file) => {
    return new Promise((resolve, reject) => {
      let reader = new FileReader()
      let imgResult = ''
      // FileReaderreadAsDataURLbase64
      reader.readAsDataURL(file)
      //error
      reader.onerror = (error) => {
        reject(error)
      }
      //load
      reader.onload = () => {
        imgResult = reader.result
      }
      //loadend
      reader.onloadend = () => {
        resolve(imgResult)
      }
    })
  },

  /**
 * 
 * @param {String} str  //
 * @param {String} type //
 * @param {Number} time //
 */
  elMessageBox: (str, type, time, onlyOne = false) => {
    if (onlyOne && document.getElementsByClassName(`el-message--${type}`).length) return;
    Message({
      message: str,
      type: type,
      duration: time || 3000,
      offset: 100 //
    });
  },

  getDate: (dateTimeStamp) => {
    if (dateTimeStamp == undefined) {
      return false;
    } else {
      let date = new Date(dateTimeStamp)
      //dateTimeStamp  
      let time = Date.UTC(
        date.getUTCFullYear(),
        date.getUTCMonth(),
        date.getUTCDate(),
        date.getUTCHours(),
        date.getUTCMinutes(),
        date.getUTCSeconds()
      );
      var now = new Date().getTime();//

      let daytime = 24 * 60 * 60 * 1000; //
      let hourtime = 60 * 60 * 1000; //
      let mintime = 60 * 1000; //
      let sectime = 1000  //

      var diffValue = now - time;

      var dayT = diffValue / daytime;
      var hourT = diffValue / hourtime;
      var minT = diffValue / mintime;
      var secT = diffValue / sectime;

      if (dayT >= 1) {
        return (parseInt(dayT) + "")
      }
      else if (hourT >= 1) {
        return (parseInt(hourT) + "")
      }
      else if (minT >= 1) {
        return (parseInt(minT) + "")
      } else if (secT >= 1 || secT <= 20) {
        return ("")
      } else {
        return (parseInt(secT) + "")
      }
    }
  },

  /**
   * 
   * @param {utc} dateTimeStamp 
   * @returns 
   */
  timeToDate: (dateTimeStamp) => {
    if (dateTimeStamp == undefined) {
      return '';
    } else {
      let date = new Date(dateTimeStamp)
      //dateTimeStamp  
      let utctime = Date.UTC(
        date.getUTCFullYear(),
        date.getUTCMonth(),
        date.getUTCDate(),
        date.getUTCHours(),
        date.getUTCMinutes(),
        date.getUTCSeconds()
      );
      let time = new Date(utctime)
      let Y = time.getFullYear() + '-';
      let M = (time.getMonth() + 1 < 10 ? '0' + (time.getMonth() + 1) : time.getMonth() + 1) + '-';
      let D = (time.getDate() < 10 ? '0' + time.getDate() : time.getDate()) + ' ';
      let h = (time.getHours() < 10 ? '0' + time.getHours() : time.getHours()) + ':';
      let m = (time.getMinutes() < 10 ? '0' + time.getMinutes() : time.getMinutes()) + ':';
      let s = (time.getSeconds() < 10 ? '0' + time.getSeconds() : time.getSeconds());
      return Y + M + D + h + m + s;
    }
  },

  getUserId: () => {
    if (!store.getters.getToken) return null;
    let userId = localStorage.getItem("id")
    if (!userId) {
      return null;
    }
    return JSON.parse(userId)
  }
}




