import axios from '@/api'
const api = '/api'

export const loginApi = (params) => { return axios.post(`${api}/login`, params) };  //
export const registerApi = (params) => { return axios.post(`${api}/register`, params) };  //
export const deleteImg = (params) => { return axios.delete(`${api}/deleteImg`, { params }) }; //

/********************************************************************************************************
 ******************************************  user   *************************************************
 ********************************************************************************************************/

export const getUserInfo = (params) => { return axios.get(`${api}/user/getInfo`, { params }) }; // 
export const updateUserInfo = (params) => { return axios.put(`${api}/user/updateInfo`, params) }; //
export const updatePassApi = (params) => { return axios.put(`${api}/user/updatePass`, params) }; //
export const uploadAvatarImgApi = (params) => { return axios.post(`${api}/user/avatar/upload`, params) }; //

